// Timer.js
import React from 'react';
import { useState, useEffect } from 'react';
import { Col, Container, Row } from 'react-bootstrap';
import styles from './Timer.module.scss';

const Timer = () => {
  const [days, setDays] = useState(0);
  const [hours, setHours] = useState(0);
  const [minutes, setMinutes] = useState(0);
  const [seconds, setSeconds] = useState(0);

  const deadline = "August, 13, 2023";

  const getTime = () => {
    const time = Date.parse(deadline) - Date.now();

    setDays(Math.floor(time / (1000 * 60 * 60 * 24)));
    setHours(Math.floor((time / (1000 * 60 * 60)) % 24));
    setMinutes(Math.floor((time / 1000 / 60) % 60));
    setSeconds(Math.floor((time / 1000) % 60));
  };

  useEffect(() => {
    const interval = setInterval(() => getTime(deadline), 1000);

    return () => clearInterval(interval);
  }, []);

  return (
    <div>
        <Container className={styles.pad_ew} >
            <Row className={`${styles.Pretendard40} ${styles.hbox} ${styles.pad_top} ${styles.timer_height}`}>
                <Col className={styles.item_center} xs={3}> {days} </Col>
                <Col className={styles.item_center} xs={3}> {hours} </Col>
                <Col className={styles.item_center} xs={3}> {minutes} </Col>
                <Col className={styles.item_center} xs={3}> {seconds} </Col>
            </Row>
            <Row className={`${styles.Pretendard16} ${styles.hbox}`}>
                <Col className={styles.item_center} xs={3}> 일 </Col>
                <Col className={styles.item_center} xs={3}> 시간 </Col>
                <Col className={styles.item_center} xs={3}> 분 </Col>
                <Col className={styles.item_center} xs={3}> 초 </Col>
            </Row>
        </Container>
    </div>
  );
};

export default Timer;